<template>
    <div class="history-wrap">
        <div class="header">
            <span class="time-date">{{ dateNow.toLocaleString() }}</span>
            <!-- <span id="current-date">{{ logStr }}</span> -->
            <div class="date-buttons">
                <button id="prev-date" @click="setLogDate(-1)">&lt;</button>
                <input type="date" name="logdate" id="logdates" v-model="logStr" @change="getHistory">
                <button id="next-date" @click="setLogDate(1)">&gt;</button>
            </div>

            <!-- とりあえず無を配置してるだけ スタイルで色々やるのもめんどそうだからこうしてる-->
            <div class="noneBlock"></div>
        </div>
        
        <div class="container">
            <div class="order-grid">
                <div class="modal history-modal">
                    <p class="loading-text">検索中...</p>
                </div>
                <div class="order-table-container">
                    <table class="order-table">
                        <thead>
                            <tr>
                                <th>伝票番号</th>
                                <th>日付</th>
                                <th>テーブル番号</th>
                                <!-- <th>人数</th> -->
                                <th>合計金額</th>
                            </tr>
                        </thead>
                        <tbody id="order-table-body" v-if="historys.length > 0">
                            <tr v-for="(order, value) in historys" :key="value" class="table-line" @click="getAbout(order)">
                                <td class="slip-id">{{ order.slip_id }}</td>
                                <td class="order-date">{{ order.transaction_datetime }}</td>
                                <td class="table-number">{{ order.table_number }}</td>
                                <!-- <td>{{ order.num_people }}</td> -->
                                <td>¥{{ order.total_price }}</td>
                            </tr>
                        </tbody>
                        <tbody v-else>
                            <tr>
                                <td colspan="5">取引がありません</td>
                            </tr>
                        </tbody>
                    </table>
                    <div class="order-table-page" v-if="orders.length/8 > 1">
                        <button class="page-btn" type="button" @click="changeIdx(1)">&lt;</button>
                        <span class="page-index">{{ pageIdx }}</span>
                        <button class="page-btn" type="button" @click="changeIdx(-1)">&gt;</button>
                    </div>
                </div>
                <div id="order-details-container" class="order-details-container">
                    <div class="order-details" v-if="details.slip_id != null">
                        <h3>取引詳細 : {{ details.slip_id }}</h3>
                        <p>来店時間: {{ details.transaction_datetime}}<br>
                        合計: ¥{{ details.bills }}<br>
                        <!-- 客数: {{ details.people }}人<br> -->
                        <!-- <p>内消費税: ¥150</p> -->
                        お預り: ¥{{ details.received }}<br>
                        おつり: ¥{{ details.received - details.bills }}</p>
                        <h4>注文商品一覧</h4>
                        <table class="order-items-table" v-if="details.orders">
                            <tr>
                                <th>商品名</th>
                                <th>注文数</th>
                                <th>価格</th>
                            </tr>
                            <tr v-for="item in details.orders" :key="item.id">
                                <td>{{ item.name }}</td>
                                <td>{{ item.amount }}</td>
                                <td>¥{{ item.total_price }}</td>
                            </tr>
                        </table>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Ajax from '@/lib/Ajax';

export default {
    data() {
        return {
            orders: [],
            historys: [],
            dateNow: new Date(),
            logDate: new Date(),
            logStr: '',
            details: {},
            pageIdx: 1
        }
    },
    mounted() {
        this.logStr = this.formatDate(this.logDate, 'yyyy-MM-dd');
        this.getHistory();
        this.$emit('setDisplay', 'history');
        setInterval(() => {
            this.dateNow = new Date();
            // (this.dateNow.getSeconds() + '.' + this.dateNow.getMilliseconds());
        }, 1000);
        this.logDate = new Date(this.dateNow.getFullYear(), this.dateNow.getMonth(), this.dateNow.getDate());
    },
    emits:[
        'setDisplay'
    ],
    methods: {
        /**
         * 日付を進めたり戻したりする処理
         * @param {*} index 
         */
        setLogDate(index) {
            
            index += this.logDate.getDate();
            this.logDate.setDate(index);
            this.logStr = this.formatDate(this.logDate, 'yyyy-MM-dd');

            this.getHistory()
        },
        /**
         * 日付ごとの取引履歴を取得する処理
         */
        getHistory() {
            const active = document.querySelector('.active');
            if(active) {
                active.classList.remove('active');
                this.details = {};
            }
            this.$el.querySelector('.history-modal').style.display = '';
            const searchStr = this.logStr;
            // fetch('/assets/json/historys/orderHistory.json').then(res => res.json()).then(dat => {
            Ajax(`/api/master/historys/${searchStr}`).then(res => res.json()).then(dat => {
                this.orders = dat;
                this.pageIdx = 1;
                setTimeout(() => {
                    this.getHistorys(0, 8);
                }, 500);
            });
            this.logDate = new Date(searchStr);
        },

        /**
         * 取引履歴の詳細情報を取得する前処理
         * @param {*} order 
         */
        getAbout(order) {
            const rows = document.querySelectorAll('.table-line');
            rows.forEach(elm => elm.classList.remove('active'));
            const conv = [].map.call(rows, elm => elm);

            const filtered = conv.filter(elm => Number.parseInt(elm.querySelector('.slip-id').textContent) === order.slip_id);
            if(filtered) {
                filtered[0].classList.add('active');
                this.getDetails(order);
            }
        },
        // getAbout(elm) {
        //     const selectedRow = elm.target.parentNode;
        //     if(selectedRow instanceof HTMLTableRowElement) {
        //         const rows = document.querySelectorAll('.table-line');
        //         rows.forEach(elm => elm.classList.remove('active'));
        //         selectedRow.classList.add('active');
        //         let slipid = selectedRow.querySelector('.slip-id').textContent;

        //         slipid = Number.parseInt(slipid);
        //         this.getDetails(slipid);
        //     }
        // },

        /**
         * 詳細情報を伝票番号から取得する処理
         * @param {*} order
         */
        getDetails(order) {
            Ajax(`/api/master/history/details/${order.slip_id}`)
            .then(res => res.json())
            .then(dat => {
                if(dat.length > 0) {
                    this.details.slip_id = order.slip_id;
                    this.details.transaction_datetime = order.transaction_datetime;
                    this.details.bills = order.total_price;
                    this.details.received = order.received;
                    this.details.orders = dat;
                }
            });
        },

        changeIdx(idx) {
            const active = document.querySelector('.active');
            if(active) {
                active.classList.remove('active');
                this.details = {};
            }
            const page = 8;
            if(idx > 0) {
                if(this.orders.length / 8 > this.pageIdx) {
                    this.pageIdx++;
                }
            } else {
                if(this.pageIdx > 1) {
                    this.pageIdx--;
                }
            }

            const start = page * (this.pageIdx - 1);
            const end = start + page;

            this.getHistorys(start, end);
        },

        /**
         * 取引履歴を開始位置から終了位置までで抽出する処理
         * @param {Number} start 
         * @param {Number} end 
         */
        getHistorys(start, end) {
            this.historys = [];
            this.orders.forEach((data, value) => {
                if(start <= value && value <= end) {
                    this.historys.push(data);
                }
            });

            this.$el.querySelector('.history-modal').style.display = 'none';
        },


        /**
         * 日付をフォーマットする関数
         * 
         * 引用元 : https://engineering.webstudio168.jp/2022/07/javascript-formatdate/
         * @param {Date} date 
         * @param {string} format 
         */
        formatDate(date, format) {
          format = format.replace(/yyyy/g, date.getFullYear());
          format = format.replace(/MM/g, ('0' + (date.getMonth() + 1)).slice(-2));
          format = format.replace(/dd/g, ('0' + date.getDate()).slice(-2));
          format = format.replace(/HH/g, ('0' + date.getHours()).slice(-2));
          format = format.replace(/mm/g, ('0' + date.getMinutes()).slice(-2));
          format = format.replace(/ss/g, ('0' + date.getSeconds()).slice(-2));
          format = format.replace(/SSS/g, ('00' + date.getMilliseconds()).slice(-3));
          return format;
        }
    }
}
</script>
